}

#callToAction {
  z-index: 1;
  margin: 0 auto;
  padding: 0;
  width: 50vw;
  -ms-grid-row: 1;
  -ms-grid-row-span: 2;
  -ms-grid-column: 2;
  grid-area: cta;
  font-size: 1.25em;
  margin-top: 50px;
}

#callToAction > p,
#callToAction h1,
#callToAction label {
  color: #f1f1f1;
}

#callToAction hr {
  width: 75%;
  margin: 0 auto;
}

#callToAction #desc {
  margin: 2vh 0 5vh 0;
  text-align: justify;
  font-size: 1em;
}

#callToAction #inputs {
  padding-bottom: 15px;
}

#callToAction #inputs > label {
  text-align: left;
}

#callToAction #inputs input {
  width: 100%;
  height: 2rem;
  font-size: 1em;
  display: inline-block;
  border: none;
  background: #f1f1f1;
  overflow: auto;
  padding: 2px;
  color: black;
  margin: 5px 0;
}

#callToAction #inputs textarea[name="body"] {
  width: 100%;
  height: 4rem;
  overflow: auto;
  resize: none;
  padding: 3px;
  font-size: 1em;
  margin: 5px 0;
}

#callToAction #inputs > button {
  height: 2.5em;
  width: 6em;
  background-color: #0f0e0c;
  color: #f1f1f1;
  font-size: 0.8em;
  border: 1px solid white;
}

#callToAction #inputs > button:hover {
  transform: scale(1.1);
  border: 1px solid #f1f1f1;
}

@media only screen and (max-width: 1000px) {
  #contactContainer {
    display: flex;
    flex-direction: column;
    padding: 0;
  }
  #contactContainer > #logo {
    margin: 0;
    padding: 0;
  }
  #contactContainer > #callToAction {
    width: calc(100vw - 2em - 10%);
    flex: 1 1;
    margin-top: 0;
  }
  #contactContainer > #callToAction > h1 {
    font-size: 1.9em;
  }
  #contactContainer > #info {
    height: 90px;
    padding: 0;
    width: calc(100vw - 2em - 10%);
    line-height: 1.5em;
  }
}
